<style id="style-mobile">
	html {
		-ms-touch-action: none;
	}

	body,
	canvas,
	div {
		display: block;
		outline: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

		user-select: none;
		-moz-user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		-khtml-user-select: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	}

	/* Remove spin of input type number */
	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		/* display: none; <- Crashes Chrome on hover */
		-webkit-appearance: none;
		margin: 0;
		/* <-- Apparently some margin are still there even though it's hidden */
	}

	body {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 0;
		border: 0;
		margin: 0;

		cursor: default;
		color: #000000;
		background-color: #333;

		text-align: center;
		font-family: Helvetica, Verdana, Arial, sans-serif;

		display: flex;
		flex-direction: column;

		/* fix bug: https://github.com/cocos-creator/2d-tasks/issues/791 */
		/* overflow cannot be applied in Cocos2dGameContainer, 
  otherwise child elements will be hidden when Cocos2dGameContainer rotated 90 deg */
		overflow: hidden;
	}

	#Cocos2dGameContainer {
		position: absolute;
		margin: 0;
		left: 0px;
		top: 0px;

		display: -webkit-box;
		-webkit-box-orient: horizontal;
		-webkit-box-align: center;
		-webkit-box-pack: center;
	}

	canvas {
		background-color: rgba(0, 0, 0, 0);
	}

	a:link,
	a:visited {
		color: #666;
	}

	a:active,
	a:hover {
		color: #666;
	}

	p.header {
		font-size: small;
	}

	p.footer {
		font-size: x-small;
	}

	#splash {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #171717 url(../assets/image/pt/startup.jpg) no-repeat center;
		background-size: 45%;
		background-position: 50%;
		background-size: cover;
	}

	.progress-bar {
		position: absolute;
		left: 27.5%;
		top: 80%;
		height: 3px;
		padding: 2px;
		width: 45%;
		border-radius: 7px;
		box-shadow: 0 1px 5px #000 inset, 0 1px 0 #444;
	}

	.progress-bar span {
		display: block;
		height: 100%;
		border-radius: 3px;
		transition: width .4s ease-in-out;
		background-color: #3dc5de;
	}

	.stripes span {
		background-size: 30px 30px;
		background-image: linear-gradient(135deg, rgba(255, 255, 255, .15) 25%, transparent 25%,
				transparent 50%, rgba(255, 255, 255, .15) 50%, rgba(255, 255, 255, .15) 75%,
				transparent 75%, transparent);

		animation: animate-stripes 1s linear infinite;
	}

	@keyframes animate-stripes {
		0% {
			background-position: 0 0;
		}

		100% {
			background-position: 60px 0;
		}
	}

	#background-img {
		background-image: url("../assets/image/pt/back.jpg");
		background-position: 50%;
	}
</style>
<style id="style">
	body {
		cursor: default;
		padding: 0;
		border: 0;
		margin: 0;

		display: flex;
		flex-direction: column;
		height: 100%;
		/* for firefox */
		width: 100%;
		position: absolute;

		text-align: center;
		background-color: white;
		font-family: Helvetica, Verdana, Arial, sans-serif;
	}

	body,
	canvas,
	div {
		outline: none;
		-moz-user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		-khtml-user-select: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	}

	/* Remove spin of input type number */
	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		/* display: none; <- Crashes Chrome on hover */
		-webkit-appearance: none;
		margin: 0;
		/* <-- Apparently some margin are still there even though it's hidden */
	}

	canvas {
		background-color: rgba(0, 0, 0, 0);
	}

	#GameDiv {
		margin: 0 auto;
		position: relative;
	}

	#Cocos3dGameContainer,
	#GameCanvas {
		width: 100%;
		height: 100%;
	}

	:root {
		--safe-top: env(safe-area-inset-top);
		--safe-right: env(safe-area-inset-right);
		--safe-bottom: env(safe-area-inset-bottom);
		--safe-left: env(safe-area-inset-left);
	}
</style>
<style id="initial-style">
	body {
		height: 100vh;
		margin: 0;
		width: 100vw
	}

	#game-shell {
		display: flex;
		height: 100%;
		position: fixed;
		width: 100%
	}

	#game-box {
		position: absolute;
	}

	#GameCanvas {
		width: 100%;
		height: 100%;
	}

	#game-overlay {
		height: 0;
		position: absolute;
		width: 0
	}

	#background-img {
		background-size: cover;
		bottom: -10%;
		height: 110%;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		width: 100%
	}

	#block-page,
	#scroll-area {
		height: 100%;
		position: absolute;
		width: 100%
	}

	#block-page {
		left: 0;
		margin: auto;
		top: 0
	}
</style>
<style id="progress-bar-style">
	#progress-bar-bg-cover {
		width: 100vw;
		height: 100vh;
		background-color: #000;
		position: fixed;
		z-index: 999;
	}

	.progress-bar-bg {
		background-color: #4d4900;
		width: 100vw;
		height: .6vh;
		min-height: 5px;
		z-index: 10;
		position: fixed;
	}

	#progress-bar-content {
		background: #59e390;
		width: 0%;
		height: .6vh;
		min-height: 5px;
	}

	.progress-bar-image {
		position: fixed;
		transform: translateY(-60%) translateX(-50%);
		top: 50%;
		left: 50%;
		z-index: 9;
	}

	@media all and (orientation : portrait) {
		.progress-bar-image {
			width: 100vw;
			height: 100vw;
		}
	}
</style>
<style id="loader-style">
	#initial-loader-1 {
		background-color: #000;
		height: 100%;
		margin: auto;
		position: fixed;
		width: 100%;
		width: 100vw;
		z-index: 98;
		height: 100vh;
		top: 0;
		left: 0;
	}

	.initial-loader-img {
		position: fixed;
		transform: translateY(-60%) translateX(-50%);
		top: 50%;
		left: 50%;
	}

	@media all and (orientation : portrait) {
		.initial-loader-img {
			width: 100vw;
			height: 100vw;
		}
	}

	#initial-loader-1,
	.svg-loading {
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: center
	}

	.circle-loading {
		align-items: center;
		display: flex;
		height: 10px;
		justify-content: space-between;
		width: 40px
	}

	.hide-loading {
		animation-duration: .35s;
		animation-fill-mode: forwards;
		animation-name: fade-out;
		animation-timing-function: ease-in
	}

	.loader-circle {
		animation-direction: alternate;
		animation-duration: .25s;
		animation-iteration-count: infinite;
		animation-name: loader-circle-bounce;
		animation-timing-function: ease-out;
		background-color: #30a2d0;
		border-radius: 50%;
		height: 10px;
		position: relative;
		width: 10px
	}

	.loader-circle:first-of-type {
		animation-delay: 0s
	}

	.loader-circle:nth-of-type(2) {
		animation-delay: -75ms
	}

	.loader-circle:nth-of-type(3) {
		animation-delay: -.15s
	}

	@keyframes loader-circle-bounce {
		0% {
			bottom: 0
		}

		90%,
		to {
			bottom: 15px
		}
	}

	@keyframes fade-out {
		to {
			opacity: 0
		}
	}

	@keyframes fade-in {
		to {
			opacity: 1
		}
	}
</style>
<style id="shell-css">
